@import '../App.scss';

table {
    font-size: small;
    margin: 0 auto;
    margin-bottom: 1rem;
    border: 1px solid $base-color1;
    text-align: center;
    width: 90%;
    border-collapse: collapse; border-spacing: 0; /* cellspacing */
}
thead {
    th {
        padding: 5px 10px;
        border: 1px solid $base-color1;
        background-color: $base-color3;
        color: $light-border;
    }
}
tbody {
    tr {
        margin: 10px;

        &:hover {
            background-color: $light-border;
        }
        &:active {
            background-color: $shadow-color;
        }
        td {
            padding: 5px 0;
            border: 1px solid $base-color1;
            
        }
    }
}
#exports {
    margin: 20px auto 0 auto;
    width: 90%;
    display: flex;
    justify-content: right;
    button {
        margin-top: 0;
        margin-right: 0;
    }
    
}

.pomiary {
    .btn-container {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        width: 90%;
        margin: 0 auto;
        .filter-btn {
            margin-top: 0;
            margin-bottom: 2rem;

        }
    }
    button {
        margin: 20px 0 10px 10px;
        padding: 10px 20px;
        background-color: $base-color1;
        color: $light-border;
        border-radius: 4px;
        border: none;
        transition: 0.2s;
        &:not(.blacked):not(.outof):hover {
            cursor: pointer;
            transform: scale(1.05);
            background-color: $base-color3;
        }
        &:not(.blacked):not(.outof):active {
            filter: brightness(80%);
            color: white;
        }
    }
    @media (max-width: 800px) {
        table {
            font-size: 8px;
        }
    }
    .pages {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        width: 90%;
        margin: 0 auto;
        button {
            margin-left: .1em;
            margin-right: 0;
            padding-left: .8em;
            padding-right: .8em;
        }
        .blacked {
            color: gray;
        }
    }
}

.selected-row {
    background-color: #ffe9e9;
}